
nav {
    opacity: .9;
    display: flex;
    justify-content: space-between;
    background-color: rgb(82, 3, 70);
    align-items: center;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    top: 0;
    width: 100%;
    overflow: hidden;
    position: fixed;
}

ul a:hover {
    background: #ddd;
    color: black;
  }
  
nav ul {
    list-style: none;
    padding-right: 30px;
}
nav ul li {
    display: inline-block;
    margin-right: 8px;
}
nav ul li a {
    text-decoration: none;
    color: bisque;
}
.nav-links {
  display:flex;
  left: 5%;
  margin: auto;
  display: block !important;
}
.nav-links i {
  color: black;
}
html {
  scroll-behavior: smooth;
}
header {
    margin: 0 auto;
    text-align: center;
}



.classA {
  font-family: 'Times New Roman', Times, serif;
  font-size: 2rem;
}

.headerImg {
    background-image: url(/a.portfolio/static/media/moonlight.25a4ba94.jpg);
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size:cover;
    background-position: center;
    padding-top: 30px;
}
.header-title {
    font-family: 'Times New Roman', Times, serif;
    margin-bottom: 30px;
    font-size: 4rem;
    color: rgb(14, 1, 14);
    padding-top: 110px;

}
  .header-p {
    margin: 5%;
    font-weight: bold;
    color: #000000;
    font-weight: 700;
    font-size: 1.75rem;
    font-family: 'Courier New', Courier, monospace;
  }

 
.about {
    width: 100%;
    height: 100vh;
}

.about-title {
    text-align: center;
    margin-top: 100px;
}
.about-content {
    display: flex;
    height: 100vh;
    justify-content: space-around;
}
.about-img2 {
    background-image: url(/a.portfolio/static/media/moonlight2.1624727a.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
